import React, { useRef, useState } from 'react';
import styles from './SignInCard.module.css';
import Copyright from '../../Components/Copyright';
import {
  Avatar,
  Button,
  CssBaseline,
  TextField,
  FormControlLabel,
  Checkbox,
  Link,
  Grid,
  Box,
  Typography,
  Container,
} from '@mui/material';
import { Link as RouterLink, useNavigate } from 'react-router-dom';
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import userSignInApi from '../../Api/userSignInApi';
import { useRecoilState } from 'recoil';
import userInfoState from '../../recoil/atoms/userInfoAtom';

const SignInCard = () => {
  const enteredEmail = useRef<any>();
  const enteredPassword = useRef<any>();
  const navigate = useNavigate();
  const [nowUserInfo, setNowUserInfo] = useRecoilState<any>(userInfoState);
  const handleSubmit = (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    const data = new FormData(event.currentTarget);
    console.log({
      email: data.get('email'),
      nickname: data.get('nickname'),
      password: data.get('password'),
    });
  };
  const signIn = () => {
    // type LoginForm = {
    //   email: string;
    //   pw: string;
    // };
    userSignInApi
      .post<any>('/userInfo/login', null, {
        params: {
          email: enteredEmail.current.value,
          pw: enteredPassword.current.value,
        },
      })
      .then((item) => {
        localStorage.setItem('access-token', item.data['access-token']);
        console.log(item);
        // alert('로그인 성공');
        setNowUserInfo({
          id: item.data.loginUser.id,
          email: item.data.loginUser.email,
          nickname: item.data.loginUser.email,
          image: item.data.loginUser.image,
          phone: item.data.loginUser.phone,
        });
        console.log(nowUserInfo);
        console.log(nowUserInfo.email);
        navigate('/');
      })
      .catch((e) => {
        console.log(e);
        alert('로그인 실패');
      });
  };

  return (
    <Container
      component="main"
      maxWidth="xs"
      sx={{ bgcolor: 'var(--eleBase-color)', marginTop: '100px' }}
    >
      <CssBaseline />
      <Box
        sx={{
          marginTop: 8,
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',
        }}
      >
        <Avatar sx={{ m: 1, bgcolor: 'var(--eleActionPos-color)' }}>
          <LockOutlinedIcon />
        </Avatar>
        <Typography component="h1" variant="h5">
          Sign in
        </Typography>
        <Box component="form" onSubmit={handleSubmit} noValidate sx={{ mt: 1 }}>
          <TextField
            margin="normal"
            required
            fullWidth
            id="email"
            label="Email Address"
            name="email"
            autoComplete="email"
            autoFocus
            inputRef={enteredEmail}
          />
          <TextField
            margin="normal"
            required
            fullWidth
            name="password"
            label="Password"
            type="password"
            id="password"
            autoComplete="current-password"
            inputRef={enteredPassword}
          />
          <FormControlLabel
            control={
              <Checkbox
                value="remember"
                sx={{ color: 'var(--eleActionPos-color)' }}
              />
            }
            label="Remember me"
          />
          <Button
            type="submit"
            fullWidth
            variant="contained"
            sx={{
              bgcolor: 'var(--eleActionPos-color)',
              color: 'var(--fontBase-color)',
              mt: 3,
              mb: 2,
            }}
            onClick={signIn}
          >
            Sign In
          </Button>
          <Grid container>
            <Grid item xs>
              <Link
                href="#"
                variant="body2"
                sx={{ color: 'var(--eleActionPos-color)' }}
              >
                <RouterLink
                  to="/findemail"
                  style={{ color: 'var(--eleActionPos-color)' }}
                >
                  Forgot email?
                </RouterLink>
              </Link>
            </Grid>
            <Grid item xs>
              <Link
                href="#"
                variant="body2"
                sx={{ color: 'var(--eleActionPos-color)' }}
              >
                <RouterLink
                  to="/findpw"
                  style={{ color: 'var(--eleActionPos-color)' }}
                >
                  Forgot password?
                </RouterLink>
              </Link>
            </Grid>
            <Grid item>
              <Link
                href="#"
                variant="body2"
                sx={{ color: 'var(--eleActionPos-color)' }}
              >
                <RouterLink
                  to="/signup"
                  style={{ color: 'var(--eleActionPos-color)' }}
                >
                  Sign UP
                </RouterLink>
              </Link>
            </Grid>
          </Grid>
        </Box>
      </Box>
      <Copyright sx={{ mt: 8, mb: 4 }} />
    </Container>
  );
};

export default SignInCard;
